import { Component, OnInit,TemplateRef, ViewChild } from '@angular/core';
import { PolicyService } from '../../../api/policy.service';
import { Router } from '@angular/router';


@Component({
	selector: 's-policy-pg',
	templateUrl: './policy.component.html',
  styleUrls: [ './policy.scss'],
})

export class PolicyComponent implements OnInit {

  //@ViewChild('severityCellTpl') severityCellTpl: TemplateRef<any>;
  //@ViewChild('statusCellTpl') statusCellTpl: TemplateRef<any>;

  //ngx-Datatable Variables
  columns:any[];
  rows:any[];

  constructor( private router: Router, private policyService: PolicyService) { }

	ngOnInit() {
    this.getPolicyData();
    this.columns=[
      {prop:"policyId"    , name: "ID"         , width:50  },
      {prop:"policyType"  , name: "Policy Type", width:150 },
      {prop:"policyName"  , name: "Policy"     , width:150 },
      {prop:"policyState" , name: "State"      , width:80  },
      {prop:"severity"    , name: "Severity"   , width:90  },
      {prop:"rules"       , name: "Rules"      , width:350 }
    ];
  }

  getPolicyData() {
      this.policyService.getPolicies().subscribe(
        resp => {
          this.rows = resp["items"].map(function(v, i, a){
            var newRow = Object.assign({}, v);
            newRow.rules="";
            return newRow;
          });
        }
      );
  }



}



